	module.exports = {"data":{"markdownRemark":{"html":"<p>Flotilla is strongly opinionated about self-service for data science.</p>\n<p>The core assumption is that you understand your work the best. Therefore, it is <em>you</em> who should own your work from end-to-end.</p>\n<ul>\n<li>\n<p>You shouldn't need to be a \"data engineer\" to extract, transform, and load data for your work. Run this work with Flotilla.</p>\n</li>\n<li>\n<p>You shouldn't need to be an \"algorithms engineer\" to run a model training job. Run this work with Flotilla.</p>\n</li>\n<li>\n<p>You shouldn't need to be a \"production engineer\" to run your production jobs or to access logs in case of problems. Do this with Flotilla.</p>\n</li>\n</ul>","frontmatter":{"path":"/docs/philosophy","title":"Philosophy","group":"docs","index":"1"}}},"pathContext":{}}

